import React from 'react';

import SLDSSection from './SLDSSection';
import { Spinner, Checkbox, Datepicker, Button, DataTable, DataTableColumn, DataTableCell } from '@salesforce/design-system-react';

import {baseUrl, GetParams, orgid} from '../lib/grax-data-lake';

import $ from 'jquery';

import '../public/js/jquery.sldsTree.js';

import GRAXSummaryChartsChart from './GRAXSummaryChartsChart';

const CustomDataTableCell = ({ children, ...props }) => (
	<DataTableCell {...props}>
		<a
			href = {baseUrl + "/grax-app/grax-data-lake/generateBackupSummary/" + props.item.spId + "/" + encodeURI(props.item.executionUniqueKey) + "?orgid=" + orgid }
			_target="_blank"
		>
			{children}
		</a>
	</DataTableCell>
);
CustomDataTableCell.displayName = DataTableCell.displayName;

export default class GRAXSummaryChartsAdvanced extends React.Component {
    constructor(props) {
        super(props);
		
		this.chart = React.createRef();

		this.summaryDataResults = [];
		this.summaryDataSelected = [];
		this.itemsObjectsByDataRange = [];
		this.itemsObjectsByDataRangeSelection = [];
		this.summaryBreadownDTItems = [];
		this.summaryBreadownDTSelection = [];
		this.summaryDTItems = [];
		this.summaryDTSelection = [];
		this.chartOptions = [];
		this.defaultChartSettings = {'width':"100%", 'height':600, "chartArea":{ width: "70%", left: "5%" } };
		this.summaryBDDTFromDate = new Date();
		this.summaryBDDTToDate = new Date();

		this.state = {
			isLoading: true,

			chartData: [],

			chartSelection: [],
			chartInput: '',
			fromDate: new Date(),
			toDate: new Date(),
			_currentOrgRecords: true,

			summaryDTFilters: {
				showRecords: true, showInserted: true, showUpdated: true, showArchived: true
			},
			
			inputValue: 'Select an option',
			selection: [],
			checked: [],

			error: null
        };
    }

    componentDidMount() {
		try{
			this.loadSummaries = function(){
				this.setState({
					isLoading: true
				});
				
				let currentChartSelected = this.state.chartSelected;

				let fromDT = this.state.fromDate;
				fromDT = fromDT.getFullYear() + '-' + (fromDT.getMonth()+1).toString().padStart(2,0) + '-' + fromDT.getDate().toString().padStart(2,0);
				let toDT = this.state.toDate;
				toDT = toDT.getFullYear() + '-' + (toDT.getMonth()+1).toString().padStart(2,0) + '-' + toDT.getDate().toString().padStart(2,0);
				
				let query = '(doc.summaryResults.startTime:>='+fromDT+') AND (doc.summaryResults.startTime:<='+toDT+')';

				if(this.state._currentOrgRecords) {
					query += ' AND (doc.graxorgid.keyword: "' + orgid + '")';
				}
				
				this.summaryBDDTFromDate = this.state.fromDate;
				this.summaryBDDTToDate = this.state.toDate;

				$.ajax({
					type: 'GET',
					
					url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
						action:'load',
						dataIndex: 'graxgrax_summaryindex',
						_select: 'summaryMap,spName,spId,CreatedDate,summaryResults,executionUniqueKey',
						_q: query,
						_limit: 1000,
						_from: 0,
						_sort: '',
						_sortType: ''
					}),
					
					success: function(data) {
						// IF NO data.results THEN LOAD FOR WORKING ONLY LOCALLY
						if( data.results ){
							this.summaryDataResults = data.results;
						}

						this.setState({
							isLoading: false,
							chartSelected: currentChartSelected
						}, function(){
							this.loadSummaryDataTable(); this.loadSummaryBreakdownTable(); this.loadObjectDataTable();
						});
					}.bind(this),
					error: function(xhr, status, err) {
						console.error(xhr, status, err);
		
						this.setState({
							isLoading: false,
						});
					}.bind(this)
				});
			};

			this.getSelectedSummary = function(){
				let selectedSummaries = this.summaryBreadownDTSelection;
				let summaryDataResults = this.summaryDataResults;
				
				let summaries4chart = [];

				selectedSummaries.forEach(function(summary){
					summaries4chart.push( summaryDataResults[summary.index] );
					/*summary.index.forEach(function(index){
						summaries4chart.push( summaryDataResults[index] );
					});*/
				});
				return summaries4chart;
			};

			this.loadSummaryDataTable = function(){
				let summaryList = this.summaryDataResults;
				let dtSummaries = [];
				let i=0;

				if( summaryList ){
					let includeArchived = this.state.summaryDTFilters.showArchived;
					//let includeRecords = this.state.summaryDTFilters.showRecords;
					let includeInserted = this.state.summaryDTFilters.showInserted;
					let includeUpdated = this.state.summaryDTFilters.showUpdated;

					summaryList.forEach(function( summary ){
						
						if( !( (includeArchived && summary.summaryResults.archived !== 0) || 
							//(includeRecords && summary.summaryResults.showRecords !== 0) || 
							(includeInserted && summary.summaryResults.inserted !== 0) || 
							(includeUpdated && summary.summaryResults.updated !== 0)) ){
							
						}else{
							let existIndex = dtSummaries.filter( function(e){return e.name === summary.spName;} );

							let archived = summary.summaryResults.archived;
							let records = summary.summaryResults.records;
							let inserted = summary.summaryResults.inserted;
							let updated = summary.summaryResults.updated;

							archived = archived?archived:0;
							records = records?records:0;
							inserted = inserted?inserted:0;
							updated = updated?updated:0;

							if( existIndex && existIndex.length > 0 ){
								existIndex[0].index.push( i );

								existIndex[0].records += records;
								existIndex[0].inserted += inserted;
								existIndex[0].updated += updated;
								existIndex[0].archived += archived;

							}else{
								let createdDate = new Date(summary.CreatedDate);
								let startTime = new Date(summary.summaryResults.startTime);
								let startTimeFormated = startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");

								dtSummaries.push( 
									{	name: summary.spName, 
										id: summary.spName, 
										spId: summary.spId,
										executionUniqueKey: summary.executionUniqueKey,
										CreatedDate: summary.CreatedDate,
										CreatedDateFormatted: createdDate.getFullYear()+"-"+(createdDate.getMonth()+1)+"-"+createdDate.getDate(),
										startTime: startTime,
										startTimeFormmated: startTimeFormated,
										records: records,
										inserted: inserted,
										updated: updated,
										archived: archived,
										index: [i]
									} 
								);
							}
						}
						i++;
					});

					dtSummaries.sort(function (a, b) {
						if (a.name > b.name) {
							return 1;
						}
				
						if (a.name < b.name) {
							return -1;
						}
						
						return 0;
					});
				}

				this.summaryDTItems = dtSummaries;
				this.summaryDTSelection = dtSummaries;
			}

			this.loadSummaryBreakdownTable = function(){
				let summaryToShow = this.summaryDTSelection;
				let summaryDataResults = this.summaryDataResults;
				let summaryBreadownDTItems = [];

				if( summaryToShow ){
					
					summaryToShow.forEach(function(dtSummary){
						dtSummary.index.forEach(function(index){
							let summary = summaryDataResults[index];

							let archived = summary.summaryResults.archived;
							let records = summary.summaryResults.records;
							let inserted = summary.summaryResults.inserted;
							let updated = summary.summaryResults.updated;

							archived = archived?archived:0;
							records = records?records:0;
							inserted = inserted?inserted:0;
							updated = updated?updated:0;
							
							let createdDate = new Date(summary.CreatedDate);
							let startTime = new Date(summary.summaryResults.startTime);
							let startTimeFormated = startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");

							summaryBreadownDTItems.push({
								name: summary.spName, 
								id: summary.Id, 
								executionUniqueKey: summary.executionUniqueKey,
								index: index, 
								CreatedDate: summary.CreatedDate,
								CreatedDateFormatted: createdDate.getFullYear()+"-"+(createdDate.getMonth()+1)+"-"+createdDate.getDate(),
								startTime: summary.startTime,
								startTimeFormmated: startTimeFormated,
								records: records,
								inserted: inserted,
								updated: updated,
								archived: archived
							});
						});
					});
				}

				this.summaryBreadownDTItems = summaryBreadownDTItems;
				this.summaryBreadownDTSelection = summaryBreadownDTItems;
			}

			this.loadObjectDataTable = function(){
				let summaryToShow = this.getSelectedSummary();
				let itemsObjectsByDataRange = [];
				let itemsObjectsByDataRangeSelection = [];

				if( summaryToShow ){
					let allSummaries = {};
					
					summaryToShow.forEach( function( summaryData ){
						var summaryMap = summaryData.summaryMap;
						
						let smyDate = new Date(summaryData.summaryResults.startTime );
						
						for( var objName in summaryMap ){
							if( typeof summaryMap[objName] == "object" ){
								let archived = summaryMap[objName].archived;
								let records = summaryMap[objName].records;
								let inserted = summaryMap[objName].inserted;
								let updated = summaryMap[objName].updated;

								archived = archived?archived:0;
								records = records?records:0;
								inserted = inserted?inserted:0;
								updated = updated?updated:0;
								
								let _allSumaries = allSummaries[objName];

								if( !_allSumaries ){
									_allSumaries = {};
									_allSumaries.name = objName;
									_allSumaries.id = objName;
									_allSumaries.archived = 0;
									_allSumaries.records = 0;
									_allSumaries.inserted = 0;
									_allSumaries.updated = 0;
									_allSumaries.startTimes = [];
								}
								_allSumaries.archived += archived?archived:0;
								_allSumaries.records += records?records:0;
								_allSumaries.inserted += inserted?inserted:0;
								_allSumaries.updated += updated?updated:0;

								let dateIndex = _allSumaries.startTimes.indexOf( smyDate );
								
								if( dateIndex < 0 ){
									dateIndex = _allSumaries.startTimes.length;

									_allSumaries.startTimes.push({
										startTime: smyDate,
										archived : 0,
										records : 0,
										inserted : 0,
										updated : 0
									});
								}

								let dateData = _allSumaries.startTimes[dateIndex];
								dateData.archived += archived?archived:0;
								dateData.records += records?records:0;
								dateData.inserted += inserted?inserted:0;
								dateData.updated += updated?updated:0;

								allSummaries[objName] = _allSumaries;
							}
						}
					});

					// Top Ten Data
					for( var objName in allSummaries ){
						itemsObjectsByDataRange.push( allSummaries[objName] );
					}

					itemsObjectsByDataRange.sort(function (a, b) {
						if (a.name > b.name) {
							return 1;
						}
				
						if (a.name < b.name) {
							return -1;
						}
						
						return 0;
					});

					itemsObjectsByDataRangeSelection = itemsObjectsByDataRange;
				}

				this.itemsObjectsByDataRange = itemsObjectsByDataRange;
				this.itemsObjectsByDataRangeSelection = itemsObjectsByDataRangeSelection;

				this.setState({
					isLoading: false,
				}, this.refreshChart );
			}.bind(this);
			
			this.refreshChart = function(){
				let chartData = [];

				let summartDataResults = this.summaryDataResults;
				//let summaryDTSelection = this.summaryDTSelection;
				let summaryBreadownDTSelection = this.summaryBreadownDTSelection;
				let itemsObjectsByDataRangeSelection = this.itemsObjectsByDataRangeSelection;

				let summaryIds = {};
				summaryBreadownDTSelection.forEach( function(summary){
					summaryIds[summary.id] = summary.id;
				});

				let objNames = {};
				itemsObjectsByDataRangeSelection.forEach(function(obj){
					objNames[obj.name] = obj.name;
				});

				summartDataResults.forEach( function(summary){
					let tempData = {};
					if( summaryIds[summary.Id] ){
						tempData.summaryMap = {};
						tempData.spName = summary.spName;
                        tempData.summaryResults= {
							archived: 0,
							records: 0,
							inserted: 0,
							update: 0,
							startTime: summary.summaryResults.startTime,
							endTime: summary.summaryResults.endTime
						};
						
						for( let key in summary.summaryMap ){
							if( objNames[key] ){
								let summaryMap = summary.summaryMap;
								tempData.summaryMap[key] = summaryMap[key];
								tempData.summaryResults.archived = summaryMap[key].archived?summaryMap[key].archived:0;
								tempData.summaryResults.records = summaryMap[key].records?summaryMap[key].records:0;
								tempData.summaryResults.inserted = summaryMap[key].inserted?summaryMap[key].inserted:0;
								tempData.summaryResults.update = summaryMap[key].update?summaryMap[key].update:0;
							}
						}

						chartData.push( tempData );
					}
				});

				this.setState({
					isLoading: false,
					chartData: chartData
				}, function(){
					this.chart.current.drawChart(); 
				});
			}.bind(this);

			this.summaryDTHandleChanged = function( event, data ){
				/*this.setState( {
					isLoading: true
				}, function(){ */
					this.summaryDTSelection = data.selection;
					this.loadSummaryBreakdownTable();
					this.loadObjectDataTable();
					//this.drawChart(); 
				//} );

				this.setState({
					isLoading: false
				});
			}.bind(this);

			this.summaryBreadownDTHandleChanged = function(event, data){
				this.summaryBreadownDTSelection = data.selection;
				this.loadObjectDataTable();
				//this.drawChart(); 

				this.setState({
					isLoading: false
				});
			}.bind(this);

			this.itemsObjectsByDataRangeHandleChanged = function( event, data ){
				/*this.setState({
					isLoading: true
				}, function(){*/
					this.itemsObjectsByDataRangeSelection = data.selection;
					
					this.refreshChart();
					//this.drawChart();
				//});
				this.setState({
					isLoading: false
				});
			}.bind(this);

			this.dataTableHandleSort = ( dtName, items, sortColumn, ...rest) => {
				const sortProperty = sortColumn.property;
				const sortDirection = sortColumn.sortDirection;
				const newState = {
					[dtName+"Column"]: sortProperty,
					[dtName+"Direction"]: {
						[sortProperty]: sortDirection,
					}
				};
		
				// needs to work in both directions
				items = items.sort((a, b) => {
					let val = 0;
		
					if (a[sortProperty] > b[sortProperty]) {
						val = 1;
					}
					if (a[sortProperty] < b[sortProperty]) {
						val = -1;
					}
		
					if (sortDirection === 'desc') {
						val *= -1;
					}
		
					return val;
				});
		
				this.setState(newState);
			};

			this.formatStartTime = function( startTime ){
				return startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");
			};

			this.formatDate = function( dt ){
				return dt.getMonth()+1+'/'+dt.getDate()+'/'+dt.getFullYear();
			}

			this.setState({
				isLoading: false
			});
			
		}catch(e){}
			
    }

    render() {
        return (
			<div style={{ minHeight: 1000 }} >
				{this.state.isLoading && (<Spinner size="large" variant="brand" />
				)}
				{/*
                <Tabs variant="scoped" id="tabs-advanced" onSelect={(value) => {this.setState( {isLoading : false} ); if(value===1) this.refreshChart(); }}>
					<TabsPanel label="Chart Settings">*/}
					<SLDSSection title="Filter Criteria" >
                            <div className="slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-1">
                                
                                <dl class="slds-list_stacked">
                                    <dt class="slds-item_label slds-text-color_weak slds-truncate" title="From">
                                        <label class="slds-form-element__label" >From</label>
                                    </dt>
                                    <dd class="slds-item_detail" >
                                        <Datepicker
                                            onChange={(event, data) => {
                                                this.setState({
                                                    fromDate: data.date
                                                });
                                            }}
                                            
                                            value={ this.state.fromDate }
                                        />
                                    </dd>
                                    <dt class="slds-item_label slds-text-color_weak slds-truncate" title="To">
                                        <label class="slds-form-element__label" >To</label>
                                    </dt>
                                    <dd class="slds-item_detail" >
                                        <Datepicker
                                            onChange={(event, data) => {
                                                this.setState({
                                                    toDate: data.date
                                                });
                                            }}

                                            value={ this.state.toDate }
                                        />
                                    </dd>
                                </dl>
                            </div>
                            <div className="slds-col slds-size_1-of-1 slds-medium-size_2-of-4 slds-large-size_1-of-4 slds-m-top_small">
                                {/*<dl class="slds-list_inline slds-grid slds-grid--vertical-align-center">
                                    <dd class="slds-item_label slds-text-color_weak slds-truncate" > */}
                                        <Checkbox
                                            className="inline-block"
                                            defaultChecked={!this.state._currentOrgRecords}
                                            onChange={(isChecked) => {
                                                this.setState({
                                                    _currentOrgRecords: !isChecked
                                                });
                                            }}
                                            assistiveText={{
                                                label: "Show records from all environments?"
                                            }}
                                            variant="toggle"
                                            labels={{
                                                toggleDisabled: 'Current Environment',
                                                toggleEnabled: 'All connected SF environments'
                                            }}
                                        />
                                    {/*</dd>
                                    <dd class="slds-item_detail slds-truncate" >*/}
                                        <div class="slds-float_right">
                                            <Button label="Retrieve" variant="brand" onClick={() => {
                                                this.loadSummaries();
                                                this.chart.current.setDateFilter( this.state.fromDate, this.state.toDate );
                                            }} />
                                        </div>
                                    {/*</dd>
                                </dl>*/}
                            </div>
                        </SLDSSection>
                        
                        <div >
                            {this.itemsObjectsByDataRange && (
                                <div>
                                    <div>
                                        <SLDSSection title="Aggregated GRAX Schedule Process" className="slds-p-top_large" isCollapsible="true"  >
                                            <div className="slds-grid slds-wrap">
                                                <div className="slds-col slds-size_10-of-12 slds-medium-size_10-of-12 slds-large-size_10-of-12">
                                                    <div style={{ height: 300}}>
                                                        <DataTable
                                                            assistiveText={{
                                                                actionsHeader: 'actions',
                                                                columnSort: 'sort this column',
                                                                columnSortedAscending: 'asc',
                                                                columnSortedDescending: 'desc',
                                                                selectAllRows: 'all rows',
                                                                selectRow: 'select this row',
                                                            }}
                                                            fixedHeader
                                                            fixedLayout
                                                            items={this.summaryDTItems}
                                                            id="DataTable_Summaries"
                                                            selection={this.summaryDTSelection}
                                                            selectRows="checkbox"
                                                            onRowChange={this.summaryDTHandleChanged}
                                                            onSort={ function(sortColumn){this.dataTableHandleSort( "DataTable_Summaries", this.summaryDTItems, sortColumn )}.bind(this) }
                                                        >
                                                            <DataTableColumn
                                                                label="GRAX Schedule Process"
                                                                sortable
                                                                primaryColumn
                                                                property="name">
																	<CustomDataTableCell />
															</DataTableColumn>

                                                            <DataTableColumn
                                                                label="Records"
                                                                sortable
                                                                property="records" />

                                                            <DataTableColumn
                                                                label="Inserted"
                                                                sortable
                                                                property="inserted" />

                                                            <DataTableColumn
                                                                label="Updated"
                                                                sortable
                                                                property="updated" />

                                                            <DataTableColumn
                                                                label="Archived"
                                                                sortable
                                                                property="archived" />

                                                        </DataTable>
                                                    </div>
                                                </div>
                                                
                                                <div className="slds-col slds-size_2-of-12 slds-medium-size_2-of-12 slds-large-size_2-of-12">
                                                    <div>
                                                        <SLDSSection title="Display Values" className="slds-p-left_small slds-m-top_none" >
                                                            <div className="slds-grid slds-wrap">
																{/* 
                                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-2 slds-x-large-size_1-of-4">
                                                                    
																	<Checkbox
                                                                        className="inline-block"
                                                                        defaultChecked={this.state.summaryDTFilters.showRecords}
                                                                        onChange={(isChecked) => {
                                                                            let summaryDTFilters = this.state.summaryDTFilters;
                                                                            summaryDTFilters.showRecords = isChecked;
                                                                            this.setState({
                                                                                summaryDTFilters: summaryDTFilters,
                                                                                isLoading : true
                                                                            }, function(){ this.loadSummaryDataTable(); this.loadSummaryBreakdownTable(); this.loadObjectDataTable(); } );
                                                                        }}
                                                                        assistiveText={{
                                                                            label: "Remove empty?"
                                                                        }}
                                                                        variant="toggle"
                                                                        labels={{
                                                                            toggleDisabled: 'Records',
                                                                            toggleEnabled: 'Records'
                                                                        }}
                                                                    />
																</div>
																*/}
                                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-2 slds-x-large-size_1-of-4">
                                                                    <Checkbox
                                                                        className="inline-block"
                                                                        defaultChecked={this.state.summaryDTFilters.showInserted}
                                                                        onChange={(isChecked) => {
                                                                            let summaryDTFilters = this.state.summaryDTFilters;
                                                                            summaryDTFilters.showInserted = isChecked;
                                                                            this.setState({
                                                                                summaryDTFilters: summaryDTFilters,
                                                                                isLoading : true
                                                                            }, function(){ this.loadSummaryDataTable(); this.loadSummaryBreakdownTable(); this.loadObjectDataTable(); } );
                                                                        }}
                                                                        assistiveText={{
                                                                            label: "Remove empty?"
                                                                        }}
                                                                        variant="toggle"
                                                                        labels={{
                                                                            toggleDisabled: 'Inserted',
                                                                            toggleEnabled: 'Inserted'
                                                                        }}
                                                                    />
                                                                </div>
                                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-2 slds-x-large-size_1-of-4">
                                                                    <Checkbox
                                                                        className="inline-block"
                                                                        defaultChecked={this.state.summaryDTFilters.showUpdated}
                                                                        onChange={(isChecked) => {
                                                                            let summaryDTFilters = this.state.summaryDTFilters;
                                                                            summaryDTFilters.showUpdated = isChecked;
                                                                            this.setState({
                                                                                summaryDTFilters: summaryDTFilters,
                                                                                isLoading : true
                                                                            }, function(){ this.loadSummaryDataTable(); this.loadSummaryBreakdownTable(); this.loadObjectDataTable(); } );
                                                                        }}
                                                                        assistiveText={{
                                                                            label: "Remove empty?"
                                                                        }}
                                                                        variant="toggle"
                                                                        labels={{
                                                                            toggleDisabled: 'Updated',
                                                                            toggleEnabled: 'Updated'
                                                                        }}
                                                                    />
                                                                </div>
                                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-2 slds-x-large-size_1-of-4">
                                                                    <Checkbox
                                                                        className="inline-block"
                                                                        defaultChecked={this.state.summaryDTFilters.showArchived}
                                                                        onChange={(isChecked) => {
                                                                            let summaryDTFilters = this.state.summaryDTFilters;
                                                                            summaryDTFilters.showArchived = isChecked;
                                                                            this.setState({
                                                                                summaryDTFilters: summaryDTFilters,
                                                                                isLoading : true
                                                                            }, function(){ this.loadSummaryDataTable(); this.loadSummaryBreakdownTable(); this.loadObjectDataTable(); } );
                                                                        }}
                                                                        assistiveText={{
                                                                            label: "Remove empty?"
                                                                        }}
                                                                        variant="toggle"
                                                                        labels={{
                                                                            toggleDisabled: 'Archived',
                                                                            toggleEnabled: 'Archived'
                                                                        }}
                                                                    />
                                                                </div>

																<div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-2 slds-x-large-size_1-of-4">
																	
																</div>
                                                            </div>
                                                        </SLDSSection>
                                                    </div>
                                                </div>
                                            </div>
                                        </SLDSSection>
                                        <SLDSSection title="GRAX Schedule Process Breakdown" className="slds-p-top_large" isCollapsible="true" >
                                        <div className="slds-grid slds-wrap">
                                                <div className="slds-col slds-size_10-of-12 slds-medium-size_10-of-12 slds-large-size_10-of-12">
                                                    <div style={{ height: 300}}>
                                                        <DataTable
                                                            assistiveText={{
                                                                actionsHeader: 'actions',
                                                                columnSort: 'sort this column',
                                                                columnSortedAscending: 'asc',
                                                                columnSortedDescending: 'desc',
                                                                selectAllRows: 'all rows',
                                                                selectRow: 'select this row',
                                                            }}
                                                            fixedHeader
                                                            fixedLayout
                                                            items={this.summaryBreadownDTItems}
                                                            id="DataTable_SummaryBreakdown"
                                                            selection={this.summaryBreadownDTSelection}
                                                            selectRows="checkbox"
                                                            onRowChange={this.summaryBreadownDTHandleChanged}
                                                            onSort={ function(sortColumn){this.dataTableHandleSort( "DataTable_SummaryBreakdown", this.summaryBreadownDTItems, sortColumn )}.bind(this) }
                                                        >
                                                            <DataTableColumn
                                                                label="GRAX Schedule Process"
                                                                sortable
                                                                primaryColumn
                                                                property="name" />
															<DataTableColumn
                                                                label="Execution Unique Key"
                                                                sortable
                                                                property="executionUniqueKey" />
                                                            <DataTableColumn
                                                                label="Start Time"
                                                                sortable
                                                                property="startTimeFormmated" />
                                                                
                                                            <DataTableColumn
                                                                label="Records"
                                                                sortable
                                                                property="records" />

                                                            <DataTableColumn
                                                                label="Inserted"
                                                                sortable
                                                                property="inserted" />

                                                            <DataTableColumn
                                                                label="Updated"
                                                                sortable
                                                                property="updated" />

                                                            <DataTableColumn
                                                                label="Archived"
                                                                sortable
                                                                property="archived" />
                                                                
                                                        </DataTable>
                                                    </div>
                                                </div>
                                            </div>
                                        </SLDSSection>
                                        <SLDSSection title="Objects" className="slds-p-top_large" isCollapsible="true" >
                                            <div className="slds-grid slds-wrap">
                                                <div className="slds-col slds-size_10-of-12 slds-medium-size_10-of-12 slds-large-size_10-of-12">
                                                    <div style={{ height: 300}}>
                                                        <DataTable
                                                            assistiveText={{
                                                                actionsHeader: 'actions',
                                                                columnSort: 'sort this column',
                                                                columnSortedAscending: 'asc',
                                                                columnSortedDescending: 'desc',
                                                                selectAllRows: 'all rows',
                                                                selectRow: 'select this row',
                                                            }}
                                                            fixedHeader
                                                            fixedLayout
                                                            items={this.itemsObjectsByDataRange}
                                                            id="DataTable_ObjectsInSummary"
                                                            selection={this.itemsObjectsByDataRangeSelection}
                                                            selectRows="checkbox"
                                                            onRowChange={this.itemsObjectsByDataRangeHandleChanged}
                                                            onSort={ function(sortColumn){this.dataTableHandleSort( "DataTable_ObjectsInSummary", this.itemsObjectsByDataRange, sortColumn )}.bind(this) }
                                                        >
                                                            <DataTableColumn
                                                                label="Object Name"
                                                                sortable
                                                                primaryColumn
                                                                property="name" />
                                                            
                                                            <DataTableColumn
                                                                label="Records"
                                                                sortable
                                                                property="records" />

                                                            <DataTableColumn
                                                                label="Inserted"
                                                                sortable
                                                                property="inserted" />

                                                            <DataTableColumn
                                                                label="Updated"
                                                                sortable
                                                                property="updated" />

                                                            <DataTableColumn
                                                                label="Archived"
                                                                sortable
                                                                property="archived" />
                                                                
                                                        </DataTable>
                                                    </div>
                                                </div>
                                            </div>
                                        </SLDSSection>
                                    </div>
                                </div>
                            ) }
                        </div>
                    {/*</TabsPanel>
                    <TabsPanel label="Chart">*/}
						<GRAXSummaryChartsChart ref={this.chart} summaryData={this.state.chartData} />
                    {/*</TabsPanel>
				</Tabs>*/}
			</div>
        )
    }
}
